import * as GWU from 'gw-utils';
const Fl = GWU.flag.fl;
///////////////////////////////////////////////////////
// CELL
export var Cell;
(function (Cell) {
    Cell[Cell["PRESSURE_PLATE_DEPRESSED"] = Fl(0)] = "PRESSURE_PLATE_DEPRESSED";
    Cell[Cell["SEARCHED_FROM_HERE"] = Fl(1)] = "SEARCHED_FROM_HERE";
    Cell[Cell["KNOWN_TO_BE_SAFE"] = Fl(2)] = "KNOWN_TO_BE_SAFE";
    Cell[Cell["CAUGHT_FIRE_THIS_TURN"] = Fl(3)] = "CAUGHT_FIRE_THIS_TURN";
    Cell[Cell["EVENT_FIRED_THIS_TURN"] = Fl(4)] = "EVENT_FIRED_THIS_TURN";
    Cell[Cell["EVENT_PROTECTED"] = Fl(5)] = "EVENT_PROTECTED";
    Cell[Cell["IS_IN_LOOP"] = Fl(6)] = "IS_IN_LOOP";
    Cell[Cell["IS_CHOKEPOINT"] = Fl(7)] = "IS_CHOKEPOINT";
    Cell[Cell["IS_GATE_SITE"] = Fl(8)] = "IS_GATE_SITE";
    Cell[Cell["IS_IN_ROOM_MACHINE"] = Fl(9)] = "IS_IN_ROOM_MACHINE";
    Cell[Cell["IS_IN_AREA_MACHINE"] = Fl(10)] = "IS_IN_AREA_MACHINE";
    Cell[Cell["IMPREGNABLE"] = Fl(11)] = "IMPREGNABLE";
    Cell[Cell["NEEDS_REDRAW"] = Fl(13)] = "NEEDS_REDRAW";
    Cell[Cell["STABLE_MEMORY"] = Fl(14)] = "STABLE_MEMORY";
    Cell[Cell["STABLE_SNAPSHOT"] = Fl(15)] = "STABLE_SNAPSHOT";
    // These are to speed checks
    Cell[Cell["HAS_PLAYER"] = Fl(16)] = "HAS_PLAYER";
    Cell[Cell["HAS_ACTOR"] = Fl(17)] = "HAS_ACTOR";
    Cell[Cell["HAS_DORMANT_MONSTER"] = Fl(18)] = "HAS_DORMANT_MONSTER";
    Cell[Cell["HAS_ITEM"] = Fl(19)] = "HAS_ITEM";
    Cell[Cell["HAS_FX"] = Fl(20)] = "HAS_FX";
    Cell[Cell["HAS_TICK_EFFECT"] = Fl(22)] = "HAS_TICK_EFFECT";
    Cell[Cell["IS_CURSOR"] = Fl(23)] = "IS_CURSOR";
    Cell[Cell["IS_HIGHLIGHTED"] = Fl(24)] = "IS_HIGHLIGHTED";
    Cell[Cell["IS_WIRED"] = Fl(26)] = "IS_WIRED";
    Cell[Cell["IS_CIRCUIT_BREAKER"] = Fl(27)] = "IS_CIRCUIT_BREAKER";
    Cell[Cell["IS_POWERED"] = Fl(28)] = "IS_POWERED";
    Cell[Cell["COLORS_DANCE"] = Fl(30)] = "COLORS_DANCE";
    Cell[Cell["CHANGED"] = Cell.NEEDS_REDRAW] = "CHANGED";
    Cell[Cell["IS_IN_MACHINE"] = Cell.IS_IN_ROOM_MACHINE | Cell.IS_IN_AREA_MACHINE] = "IS_IN_MACHINE";
    Cell[Cell["PERMANENT_CELL_FLAGS"] = Cell.HAS_ITEM |
        Cell.HAS_DORMANT_MONSTER |
        Cell.STABLE_MEMORY |
        Cell.SEARCHED_FROM_HERE |
        Cell.PRESSURE_PLATE_DEPRESSED |
        Cell.KNOWN_TO_BE_SAFE |
        Cell.IS_IN_LOOP |
        Cell.IS_CHOKEPOINT |
        Cell.IS_GATE_SITE |
        Cell.IS_IN_MACHINE |
        Cell.IMPREGNABLE] = "PERMANENT_CELL_FLAGS";
    Cell[Cell["HAS_ANY_ACTOR"] = Cell.HAS_PLAYER | Cell.HAS_ACTOR] = "HAS_ANY_ACTOR";
    Cell[Cell["HAS_ANY_OBJECT"] = Cell.HAS_ITEM | Cell.HAS_ANY_ACTOR] = "HAS_ANY_OBJECT";
    Cell[Cell["CELL_DEFAULT"] = Cell.NEEDS_REDRAW] = "CELL_DEFAULT";
})(Cell || (Cell = {}));
