import * as GWU from 'gw-utils';
import { installHandler } from '../effect';
//////////////////////////////////////////////
// MESSAGE
export function makeMessageHandler(src) {
    if (Array.isArray(src))
        src = src[0];
    if (typeof src !== 'string') {
        throw new Error('Need message for message effect.');
    }
    const info = {
        msg: src,
    };
    return messageEffect.bind(undefined, info);
}
export function messageEffect(info, loc, ctx) {
    const seen = ctx.seen;
    const msg = info.msg;
    if (msg &&
        msg.length &&
        ctx.aware &&
        !seen
    // && map.isVisible(x, y)
    ) {
        GWU.message.addAt(loc.x, loc.y, msg, ctx);
        return true;
    }
    return false;
}
installHandler('msg', makeMessageHandler);
