// TODO - Do we need the machine?
export class KeyInfo {
    constructor(x, y, disposable) {
        this.x = x;
        this.y = y;
        this.disposable = disposable;
    }
    matches(x, y) {
        return this.x === x && this.y === y;
    }
}
export function makeKeyInfo(x, y, disposable) {
    return new KeyInfo(x, y, disposable);
}
